.processes-container{position:fixed;z-index:7000;top:0;left:50%;width:350px;margin-left:-175px;word-break:break-all}.process-spinner{position:relative;padding:0 10px;line-height:24px;border-radius:0 0 3px 3px;box-sizing:border-box;background-color:#f0e8ac;text-align:center;opacity:0;transition:all .2s ease}.process-spinner.shown{opacity:1}.process-spinner+.process-spinner{margin-top:5px;border-radius:3px}.process-spinner-failed{padding-right:30px;background-color:#d4333f;color:#fff}.process-spinner-success{padding-right:30px;background-color:#0a0;color:#fff}.process-spinner-close{position:absolute;top:0;right:0;padding:3px;background:none!important;border:none!important;line-height:1;color:#fff}.shortcuts-section+.shortcuts-section{margin-top:20px}.shortcuts-section-title{margin-bottom:5px}.shortcuts-list>li+li{margin-top:5px}.shortcut-button{display:inline-block;min-width:24px;height:24px;line-height:21px;padding:0 4px;box-sizing:border-box;border:1px solid #ccc;border-radius:3px;background-image:linear-gradient(180deg,#f5f5f5,#eee);box-shadow:inset 0 1px 0 #fff,0 1px 0 #ccc;color:#777;font-size:11px;text-align:center}.shortcut-button-small{min-width:16px;height:16px;line-height:14px;margin-left:4px;margin-right:4px}.flash{background-color:transparent;transition:all .5s ease}.flash.in{background-color:#fcf8e3}.flash-heavy.in{background-color:#ffe456}.identity-provider{display:inline-block;line-height:14px;padding:2px 5px;border:1px solid rgba(0,0,0,.15);border-radius:3px;box-sizing:border-box;background-color:#236a97;font-size:12px;color:#fff}