.boxed-group{margin-bottom:20px;border:1px solid #e6e6e6;border-radius:2px;background-color:#fff}.boxed-group>h2{line-height:24px;padding:16px 20px 0}.boxed-group hr{height:0;border-top:1px solid #efefef;margin:16px -20px}.boxed-group-header{position:relative;z-index:10;padding:16px 20px 0}.boxed-group-header>h2{line-height:24px}.boxed-group-header>h2,.boxed-group-header [class^=icon-]{display:inline-block;vertical-align:middle}.boxed-group-actions{position:relative;z-index:12;float:right;margin-top:16px;margin-right:20px}.boxed-group-inner{padding:16px 20px}.boxed-group-inner:empty{padding-top:0}.boxed-group-list{margin-top:- 8px;margin-bottom:- 8px}.boxed-group-list>li{margin-left:-20px;margin-right:-20px;padding:8px 20px}.boxed-group-accordion{margin-bottom:8px;transition:border-color .3s ease}.boxed-group-accordion:not(.no-hover):hover{border-color:#4b9fd5}.boxed-group-accordion:not(.no-hover):hover .boxed-group-accordion-title{color:#4b9fd5}.boxed-group-accordion .boxed-group-header{cursor:pointer;padding-bottom:16px}.boxed-group-accordion .boxed-group-header>.alert{display:inline-block;margin-bottom:-6px;margin-top:-6px}.boxed-group-accordion .boxed-group-inner{padding-top:0}.boxed-group-accordion-title{font-weight:700;transition:color .3s ease}