.tooltip{position:absolute;z-index:8000;display:block;height:auto;box-sizing:border-box;font-size:12px;font-weight:300;line-height:1.5;animation:fadeIn .3s forwards}.tooltip.top{padding:5px 0;margin-top:-3px}.tooltip.right{padding:0 5px;margin-left:3px}.tooltip.bottom{padding:5px 0;margin-top:3px}.tooltip.left{padding:0 5px;margin-left:-3px}.tooltip-inner{max-width:300px;padding:3px 8px;color:#fff;text-align:left;text-decoration:none;background-color:#475760;border-radius:4px;letter-spacing:.04em;overflow:hidden;word-break:break-word}.tooltip-inner .alert{margin-bottom:5px;border-radius:4px}.tooltip-arrow{position:absolute;width:0;height:0;border:solid transparent}.tooltip.top .tooltip-arrow{bottom:0;left:50%;border-width:5px 5px 0;border-top-color:#475760;transform:translateX(-5px)}.tooltip.right .tooltip-arrow{top:50%;left:0;transform:translateY(-5px);border-width:5px 5px 5px 0;border-right-color:#475760}.tooltip.left .tooltip-arrow{top:50%;right:0;transform:translateY(-5px);border-width:5px 0 5px 5px;border-left-color:#475760}.tooltip.bottom .tooltip-arrow{top:0;left:50%;transform:translateX(-5px);border-width:0 5px 5px;border-bottom-color:#475760}@keyframes fadeIn{0%{opacity:0}to{opacity:1}}