.organization-switch{display:inline-block}.organization-switch .dropdown-toggle{display:flex;align-items:center;height:32px;padding:0 8px;border:1px solid transparent;border-radius:2px;box-sizing:border-box;color:#444!important;transition:all .3s ease}.organization-switch .dropdown-toggle:hover,.organization-switch.open .dropdown-toggle{border-color:#e6e6e6;background-color:#fff;box-shadow:0 6px 12px rgba(0,0,0,.175)}.organization-switch.open .dropdown-toggle{border-bottom-color:transparent;border-bottom-left-radius:0;border-bottom-right-radius:0}.organization-switch .dropdown-menu{min-width:100%;margin-left:-1px}.organization-switch .dropdown-menu>li>a{padding-left:8px;padding-right:8px}